import { DragInlineProps, DragFragmentProps, DRAG_TYPE } from '../types';

/**
 * 判断 fragment 是否是图片
 * @param fragment
 * @returns
 */
function isImageFragment(fragment: DragFragmentProps) {
  if (fragment.type === DRAG_TYPE.inline) {
    const { type } = fragment.fragment as DragInlineProps;
    return type === 'image';
  }

  return false;
}

export default isImageFragment;
